import '../../style/components/_Auth.scoped.sass';
import * as Component from '../../components/styled/card'
import {AuthLayer} from '../../components/global/Components';
import { Link } from 'react-router-dom';
import React from "react";
import {userAction} from "../../store/actions";
import {_storage} from "../../helpers/helper";
const lang = _storage.get('current_lang') ? _storage.get('current_lang') : 'ru'

const Login = ({form,setForm,error}) => {
    
    let pageContent = {}
    if(lang =='kz'){

        pageContent ={
            main: ' ',
            email: '  ',
            pass: ' ',
            getPass:'   ?',
            rememberMe:'  ',
            login:' ',
            reg:'',
            acc:'   ?',
        }
    }
    if(lang =='ru'){

        pageContent ={
            main: ' ',
            email: ' e-mail',
            pass: ' ',
            getPass:' ?',
            rememberMe:' ',
            login:'',
            reg:'',
            acc:'  ?',
            
        }
    }
    if(lang =='en'){
        pageContent ={
                main: 'Main Page',
                email: 'Your email',
                pass: 'Your password',
                getPass:'Forgot password?',
                rememberMe:'Remember me',
                login:'Login',
                reg:'Sign in',
                acc:'Register',
            }
    }
    const onInputChange = (e) => {
        setForm({
            ...form,
            [e.target.name] : e.target.name === 'remember' ? e.target.checked : e.target.value
        })
    }

    return (
        <>
            <p>
                <label htmlFor="">{pageContent.email}</label>
                <Component.InputText width="100%" type="email" required placeholder="Email" name="email" onChange={onInputChange}/>
            </p>
            <p>
                <label htmlFor="">{pageContent.pass}</label>
                 <Component.InputText width="100%" required type="password" name="password" placeholder="&bull;&bull;&bull;&bull;&bull;" onChange={onInputChange}/>
            </p>
            {
                error ?
                    <p className="errors">
                        <span>{error}</span>
                    </p>
                    : null
            }
            <p className="forgot">
                <Component.customCheckbox>
                    <input type="checkbox" name="remember"  id="till15" onChange={onInputChange}/>
                    <span/>
                    <label htmlFor="till15">{pageContent.rememberMe}</label>
                </Component.customCheckbox>
                <Link to="forget">
                    {pageContent.getPass}
                </Link>
            </p>
            <p className="enter">
                <Component.Button padding="0.6rem 5rem">{pageContent.login}</Component.Button>
            </p>
            <p className="noaccount">
                <span>{pageContent.acc}</span>
                <Link to="/register">{pageContent.reg}</Link>
            </p>
        </>
    )
}

    export default AuthLayer(Login,{breadcump: "  ", title: "",action: userAction});
